
.hero {
	max-height:1024px;
	max-width:1450px;
	background:$vermelho;
	font-size:0;
	height:700px;
	width:100%;
	background-size:cover;
	background-position: center; 
	background-image:url('../images/hero.png');	
	.scroll_gif {
	   	position: absolute; 
	    width: 95px;
	    height: 70px;
	    left: 50%;
	    transform: translate(-50%);
		top: 90%;
		@include responsive(0,1000px) {
			top:62%;
		}	
		@include responsive(0,800px) {
			top:55%;
		}	
		@include responsive(0,768px) { 
			display:none;
		}	
	}
	h1 {
		font-size:72px;
		width:240px;
		position:absolute;
		color:$vermelho;
		left:120px;
		top:310px;
		@include responsive(0,1130px) {
			top:300px;
			font-size:55px;
		}
		@include responsive(0,1000px) {
			top: 200px;
		    font-size: 50px;
		    width: 200px;
		}	
		@include responsive(0,770px) {
			top: 200px;
		    font-size: 50px;
		    width: 100%;
		    left:0;
		    text-align:center;
		}	
	}
}
